@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500;1,600&display=swap');
:root{
  --bg-black-900:#181818;
  --bg-black-100:#383838;
  --bg--black-50:#e8dfec;
  --skin-color:#eebb99;
  --text-black-900:	#FFFFFF;
  --text-black-700:#FFFFFF;
  --bg-black-1000:#000;
}
body{
  /* line-height: 1.5; */
  font-size: 16px;
  background-color: var(--bg-black-900);
  font-family: 'Poppins' sans-serif;
}
*{
  margin: 0;
  padding:0;
  outline:none;
  text-decoration: none;
  box-sizing: border-box;
}
::before,::after{
  box-sizing: border-box;
}


/* SIdebar */

.sidebar{
  position: fixed;
  left:-250px;
  height:100vh;
  width:250px;
  background:var(--bg-black-100);
  transition: all .5 ease;
  z-index:1111;
  margin-top: -25px;
  justify-content: center;
align-items: center;
}
.sidebar header{
  font-size: 22px;
  color:#fff;
  text-align: center;
  line-height: 70px;
  background: var(--bg-black-100);
  user-select: none;
  color: var(--skin-color);
  text-transform: uppercase;
  letter-spacing: 5px;
}
.sidebar ul {

  list-style: none;
  margin-top:40px;
}
.sidebar ul .a{
  display: block;
  height: 100%;
  width: 100%;
  line-height: 65px;
  font-size: 20px;
  color:#fff;
  text-decoration: none;
  padding-left: 40px;
  box-sizing: border-box;
  border-top:1px solid rgba(255,255,255,.1);
  border-bottom: 1px solid #000;
  transition: .4s;
}
ul li:hover .a{
  padding-left:50px;
}

.sidebar ul .a .fas{
  margin-right:16px;
}

#check{
  display: none;
}
label #btn {
position: absolute;
cursor: pointer;
background:#042331;
border-radius:3px;
}
label #cancel{
position: absolute;
cursor: pointer;
background: var(--bg-black-700);
border-radius:3px;
}
label #btn{
  left:40px;
  top:25px;
  font-size:35px;
  color:#fff;
  padding:6px 12px;
  transition: all .5s ease;
}
label #cancel{
  z-index: 11111;
  position: fixed;
  left:-195px;
  top:17px;
  font-size: 30px;
  color:#fff;
  padding:6px 12px;
  transition: all .5s ease;
  }
  #check:checked ~ .sidebar{
    left:0
  }
  #check:checked ~ label #btn{
    left:250px;
    opacity: 0;
    pointer-events: none;
  }
  #check:checked ~ label #cancel{
    left:195px;
  }
  #check:checked ~ .main-content{
    padding-left:250px;
    background-size:cover;
    height: 100vh;
    background-position: center;
    transition: all .5s ease;
  }
ul{
  list-style: none;
  text-decoration: none;
}
.section{
  background: var(--bg-black-900);
  min-height: 100%;
  display: block;
  padding:0 30px;
  opacity: 1;
}
.section .active{
  z-index: 2;
  opacity:1;
}
/* .hidden{
  display: none !important;
} */
.main-content{
  padding-left: 270px;
}
.padd-15{
  padding-left: 15px;
  padding-right: 15px;
}
.container{
  max-width: 1100px;
  width: 100%;
  margin: auto;
}
.section .container{
  padding-top: 60px;
  padding-bottom: 70px;
}
.section-title{
  flex:0 0 100%;
  max-width: 100%;
  margin-bottom: 60px;
}
.section-title h2{
  font-size:40px;
  color: var(--skin-color);
  font-weight: 700;
  position: relative;
}
.section-title h2::before{
  content: '';
  height:4px;
  width: 50px;
  background-color: var(--skin-color);
  position: absolute;
  top:100%;
  left: 0;
}
.section-title h2::after{
  content: '';
  height:4px;
  width: 25px;
  background-color: var(--skin-color);
  position: absolute;
  top:100%;
  left: 0;
  margin-top:8px;
}
.row{
  display: flex;;

  flex-wrap: wrap;
  margin-left: -15px;
  margin-right:-15px ;
  position: relative;
}
.btn{
  font-size:16px;
  font-weight: 500;
  padding: 12px 35px;
  background: var(--skin-color);
  color: #fff;
  border-radius: 40px;
  border:none;
  display: inline-block;
  white-space: nowrap;
  transition: all 0.5s ease;
}
.btn:hover{
 transform: scale(1.05);
}

