/* Home */
.home{
  min-height: 90vh;
  display: flex;
  color: var(--text-black-900);
}
.home .home-info{
  flex:0 0 60%;
  max-width: 60%;
}
h3 .hello{
  font-size: 28px;
  margin:15px 0;
}
.hello span{
  font-family: 'Clicker Script', cursive;
  font-size: 25px;
  font-weight: 700;
  color: var(--skin-color);
}
h3 .my-profession{
  font-size: 30px;
  margin: 15px 0;
}
.typing{
  color: var(--skin-color);
}
.home-info p{
  margin-bottom: 70px;
  font-size: 20px;
  color: var(--text-black-700);
}
.home .home-img{
  flex: 0 0 40%;
  max-width: 40%;
  text-align: center;
}
.home .home-img img{
  height:400px;
  margin:auto;
  border-radius: 5px;
}
.home-img::before{
  content: '';
  position: absolute;
  height:80px;
  width: 80px;
 
  left:-20px;
  top:-40px;
}
.home-img::after{
  content: '';
  position: absolute;
  height:80px;
  width: 80px;
  right:20px;
  bottom:-40px;
}
/* About */
.about .about-content{
  flex:0 0 100%;
  max-width:100%;
}
.about .about-content .about-text{
  flex: 0 0 100%;
  max-width: 100%;
}
.about .about-content .about-text h3{
  font-size: 24px;
  margin-bottom:15px;
  font-weight: 700;
}
.about .about-content .about-text h3 span{
  color:var(--skin-color)
}
.about .about-content .about-text p{
  font-size: 16px;
  line-height: 25px;
  color: var(--text-black-700);
  text-align: justify;
}
.about .about-content .personal-info{
  flex: 0 0 100%;
  flex:wrap;
  max-width: 80%;
  margin-top: 40px;
}
.about .about-content .personal-info .info-item{
  flex: 0 0 100%;
  max-width: 50%;
}
.about .about-content .personal-info .info-item p{
  font-family: 600;
  padding:10px 0;
  font-size:16px;
  color: var(--text-black-900);
  border-bottom: 1px solid var(--bg--black-50);
}
.about .about-content .personal-info .info-item p span{
  font-weight: 500;
  color: var(--text-black-900);
  margin-left:4px;
  display:inline-block
}
.about .about-content .buttons .btn{
  flex:0 0 100%;
  margin-top:10px;
  margin-left:10px ;
}
.about .about-content .container .inner{
  padding:20px;
}
.about .about-content .inner .text-center{
  color: var(--text-black-700);
}
.about .about-content .container .inner .header{
  text-align: center;
  position:absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  height: 4px;
  width: 100%;
  scroll-padding-block-start: #2ecc71;
}
.about .about-content .container{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  justify-content: center;
  align-items:center;
  text-align: center;
  grid-gap:1rem;
  padding: 1rem 80px;
  font-size: 1.2rem;
}
.about .about-content .container .skill-box{
padding: 1.5rem;
color: #ddd;
cursor: pointer;
margin:auto;
}
.about .about-content .container .skill-box:hover > .skill-tilte:after, .skill-box:hover > .skill-tilte:before{
  width:35px;
}
.about .about-content .container .skill-box:hover .skill-icon{
  transform: translateY(-8px);
}
.about .about-content .container .skill-box .skill-tilte{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
}
.about .about-content .container .skill-box .skill-tilte:after{
  content: '';
  position: absolute;
  bottom: 0;
  right: 50%;
  width: 35px;
  height: 4px;
  border-radius: 2px 0 0 2px;
  background-color: #2ecc71;
  transition: .5s;
}
.about .about-content .container .skill-box .skill-tilte:before{
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 4px;
  border-radius: 0 2px 2px 0;
  background-color: #2ecc71;
}
.about .about-content .container .skill-img .skill-icon {
  width:90px;
  height:90px;
  position: relative;
  border-radius: 45px;
  background-color: #fff;
  display: flex;
}
.about .about-content .container .skill-img .skill-icon:after{
  content: '';
  position: absolute;
  top:0;
  left:0;
  width: 50%;
  height:90%;
  background-color: rgba(100, 1000, 100, 0.5);
  border-radius:45px 0 0 45px ;
}
.about .about-content .container .skill-img .skill-tilte h3{
  color: #fff;
  margin:0.5rem;
}
@media screen and (max-width:990px){
  .about .about-content .container{
    grid-template-columns: repeat(3, 1fr);
    padding: 2rem 50px;
  }
}
@media screen and (max-width:660px){
  .about .about-content .container{
    grid-template-columns: repeat(2, 1fr);
    padding: 2rem 50px;
  }
}
@media screen and (max-width:360px){
  .about .about-content .container{
    grid-template-columns: repeat(2, 1fr);
    padding: 2rem 30px;
  }
}

/* Services */
.services{
  margin-bottom: 30px;
}
.serv{
  /* background-color:var(--bg-black-1000); */
  color:#000;
  height: auto;
}
.services h1,p,h3{
  color: var(--text-black-900);
}
.service-box{
  box-shadow: 0 0 4px 0 #ffa701;
  padding:15px;
  position: relative;
  margin-top: auto;
}
.service-box .fa{
  font-size: 40px ;
  margin-bottom: 20px;
  color:var(--skin-color);
}
.service-box p{
  text-align: justify;
}
.service-box span{
  color: var(--skin-color);
}
.services  span{
  color: var(--skin-color);
}


/* Responsive */
@media (max-width:1199px)
{
  .aside{
    left:-270px;
  }
  .aside .nav-toggler{
    display:flex;
    left: 30px;
 }
.main-content{
  padding-left: 0;
}
  .section{
    left: 0;
  }
  .about .about-content .personal-info .info-item p span{
    display: block;
    margin-left: 0;
  }
}
@media (max-width:991px){
  .home .home-info{
    flex: 0 0 100%;
    max-width: 100%;
  }
  .home .home-img{
    display: none;
  }
}
@media (max-width:767px){
  .about .about-content .personal-info {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

/* Protfolio */
.portfolio .container .heading{
font-size:3rem;
text-transform:capitalize;
color: var(--skin-color);
font-family: Verdana, Geneva, Tahoma, sans-serif;
letter-spacing: .2rem;
text-align: center;
text-shadow: 0 .3rem .5rem rgba(0,0,0,.5);
}
.portfolio .container .img-container{
  display:flex;
  justify-content: center;
  flex-wrap: wrap;
}
.portfolio .img-container .img{
  height:15rem;
  width:25rem;
  margin:2rem;
  box-shadow:0 .5rem 1rem rgba(0,0,0,.5);
  overflow: hidden;
  border-radius: 1rem;
}
.portfolio .img-container .img img{
  width: 100%;
  height:100%;
  object-fit: cover;
  outline: none;
  border:none;
  cursor:pointer
}
.portfolio .img-container .img img:hover{
transition: .2s linear;
transform: scale(1.3);
}
